.Navbar-Container {
    height: 60px;
  }
  

  
  .Navbar-Wrapper {
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  /* @media screen and (max-width: 768px) {
    .Navbar-Wrapper {
      padding: 10px 0px;
      flex-direction: column;
    }
  } */
  
  .Navbar-Left,
  .Navbar-Center,
  .Navbar-Right {
    flex: 1;
    display: flex;
    align-items: center;
  }
  
  .Navbar-Language {
    font-size: 14px;
    cursor: pointer;
  }

  
  .Navbar-SearchContainer {
    border: 0.5px solid lightgray;
    display: flex;
    align-items: center;
    margin-left: 25px;
    padding: 5px;
  }
  
  .Navbar-Input {
    border: none;
  }
  
 
  
  .Navbar-Logo {
    font-weight: bold;
    font-size: 24px;
  }
  
 
  
  .Navbar-MenuItem {
    font-size: 14px;
    cursor: pointer;
    margin-left: 25px;
  }
  
  @media screen and (max-width: 768px) {
    .Navbar-MenuItem {
      font-size: 12px;
      margin-left: 10px;
    }
    .Navbar-Container {
        height: 50px;
      }
      .Navbar-Logo {
        font-size: 18px;
      }
      .Navbar-Input {
        width: 50px;
      }
      .Navbar-Language {
        display: none;
      }
  }
 