.slider-container {
    min-width: 400px;
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
  }
  
  .arrow {
    width: 50px;
    height: 50px;
    background-color: #fff7f7;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    opacity: 0.5;
  }
  
  .arrow.left {
    left: 10px;
  }
  
  .arrow.right {
    right: 10px;
  }
  
  .arrow-icon {
    font-size: 24px;
  }
  
  .slides-wrapper {
    height: 100%;
    display: flex;
    transition: all 1.5s ease;
  }
  
  .slide {
    width: 100vw;
    height: 100vh;
    display: none;
  }
  
  .slide.active {
    display: flex;
  }
  
  .img-container {
    height: 100%;
    flex: 1;
  }
  
  .image {
    height: 100%;
  }
  
  .info-container {
    flex: 1;
    padding: 50px;
  }
  
  .title {
    font-size: 70px;
  }
  
  .desc {
    margin: 50px 0px;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 3px;
  }
  
  .button {
    padding: 10px;
    font-size: 20px;
    background-color: transparent;
    cursor: pointer;
  }
  
  @media screen and (max-width: 768px) {
    .title {
        font-size: 40px;
      }
    .slider-container{
        display: none;
    }
    
      .desc {
        font-size: 16px;
      }
  }
  
  